___CSS_LOADER_EXPORT___.push([module.id, `
.search-term-container {
	display: flex;
	flex-wrap: wrap;
	max-width: 350px;
}
.style-chooser.v-select {
	max-width: 350px;
	min-width: calc(100% - 125px);
	width: 100%;
	margin: 0;
	text-transform: none;
}
#term {
	min-width: 100%;
	height: 40px;
	margin: 0 5px 0 0;
}
@media screen and (max-width: 767px) {
#term {
		margin-bottom: 5px;
}
}
.style-chooser .vs__search {
	border: none !important;
}
.style-chooser .vs__dropdown-toggle {
	min-height: 40px;
	padding-bottom: 0;
}
.style-chooser .vs__actions {
	display: none;
}
.style-chooser .vs__selected {
	border: none;
	max-height: 32px;
	color: #707070;
	background: #EDEDED;
}
.style-chooser .vs__selected-options {
	min-height: 36px;
}
.style-chooser .vs__dropdown-option--highlight {
	color: #707070;
	background: #EDEDED;
}
.style-chooser .vs__dropdown-option--selected {
	color: #EDEDED;
	background: #707070;
}
.style-chooser .vs__dropdown-menu {
	top: calc(100% - 6px);
}
.style-chooser input.vs__search::placeholder {
	text-transform: none !important;
	font-variant: normal !important;
	font-variant-caps: normal !important;
	font-size: 18px !important;
	color: rgb(111, 111, 111) !important;
}
.style-chooser input.vs__search {
	padding-top: 6px !important;
	padding-bottom: 5px !important;
}
.style-chooser button.vs__deselect {
	display: flex;
	align-items: center;
}
#search-arrow {
	cursor: pointer;
}
.app-tuuls_dashboard #filestable #fileList tr td {
	border-bottom: none;
}
.app-tuuls_dashboard #filestable #fileList tr td:not(:first-of-type) {
	padding-left: 1em;
}
.app-tuuls_dashboard #filestable #fileList .thumbnail {
	display: grid;
	place-items: center;
}
.app-tuuls_dashboard #filestable #fileList .thumbnail img {
	width: 100%;
	max-height: 64px;
	object-fit: contain;
}
.fullwidth-toggle-btn {
	background: transparent;
	border: 1px solid var(--color-border-dark);
	border-radius: var(--border-radius);
	padding: 6px;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 34px;
	height: 34px;
	transition: background-color 0.2s ease;
}
.fullwidth-toggle-btn:hover {
	background: var(--color-background-hover);
}
.fullwidth-toggle-btn.active {
	background: var(--color-primary-element);
	border-color: var(--color-primary-element);
}
.fullwidth-toggle-btn .icon {
	width: 16px;
	height: 16px;
	opacity: 0.7;
}
.fullwidth-toggle-btn:hover .icon {
	opacity: 1;
}
.view-toggle-btn {
	background: transparent;
	border: 1px solid var(--color-border-dark);
	border-radius: var(--border-radius);
	padding: 6px;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 34px;
	height: 34px;
	transition: background-color 0.2s ease;
}
.view-toggle-btn:hover {
	background: var(--color-background-hover);
}
.view-toggle-btn.active {
	background: var(--color-primary-element);
	border-color: var(--color-primary-element);
}
.view-toggle-btn .icon {
	width: 16px;
	height: 16px;
	opacity: 0.7;
}
.view-toggle-btn:hover .icon {
	opacity: 1;
}
.grid-view .filestable {
	--thumbail-size: 230px;
	--thumbail-margin: 4px;
}

/* Grid view styles */
.grid-view .filestable tbody {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
	gap: 8px;
	padding: 16px 0;
}



/* Thumbnail overlay - only visible in grid view */
.thumbnail-overlay {
	display: none;
}
.grid-view .thumbnail-overlay {
	display: block;
	position: absolute;
	bottom: 0.5em;
	left: var(--thumbail-margin);
	right: var(--thumbail-margin);
	height: 75%;
	max-height: calc(var(--thumbail-size) * 0.8);
	background: var(--color-background-dark);
	z-index: 5;
	opacity: 0;
	transition: opacity 0.3s ease;
	overflow-y: auto;
	overflow-x: hidden;
	border-radius: var(--border-radius);
	box-sizing: border-box;
	width: 98%;
	max-width: 98%;
}
.grid-view .thumbnail-overlay:hover {
	opacity: 0.8;
}
.grid-view .overlay-content {
	padding: 8px 10px;
	color: var(--color-primary-text);
	font-size: 0.8em;
	line-height: 1.3;
	width: 100%;
	box-sizing: border-box;
}
.grid-view .overlay-field {
	margin-bottom: 4px;
	word-wrap: break-word;
	overflow-wrap: break-word;
	word-break: break-word;
	hyphens: auto;
	width: 100%;
}
.grid-view .overlay-field:last-child {
	margin-bottom: 0;
}
.grid-view .overlay-label {
	font-weight: 600;
	margin-right: 4px;
	white-space: nowrap;
}
.grid-view .overlay-value {
	color: var(--color-primary-text);
	word-wrap: break-word;
	overflow-wrap: break-word;
	word-break: break-word;
	white-space: normal;
}
.grid-view .overlay-label {
	font-weight: bold;
}
.grid-view table.filestable {
	display: block;
}
.grid-view .filestable tbody tr {
	display: flex;
	flex-direction: column;
	align-items: center;
	background: var(--color-background-hover);
	border-radius: var(--border-radius);
	padding: 8px 0 8px 0;
	height: auto;
	min-height: 200px;
	border-bottom: none;
	text-align: center;
}
.grid-view .filestable tbody tr td {
	border-bottom: none;
	padding: 4px 0;
	width: 100%;
	display: flex;
	justify-content: center;
}
.grid-view .filestable tbody tr td.filename {
	order: 2;
	flex-direction: column;
	min-width: 100%;
	height: 1.5em;
	align-items: center;
}
.grid-view .filestable tbody tr td.size {
	order: 3;
	font-size: 0.9em;
	color: var(--color-text-maxcontrast);
}
.grid-view .filestable tbody tr td.modified {
	order: 4;
	font-size: 0.9em;
	color: var(--color-text-maxcontrast);
}
.grid-view .filestable tbody tr td.beschreibung {
	order: 5;
	font-size: 0.9em;
	color: var(--color-text-maxcontrast);
}
.grid-view .filestable tbody tr td.tags {
	order: 6;
	font-size: 0.9em;
	color: var(--color-text-maxcontrast);
}
.grid-view .filestable tbody tr td.info {
	order: 7;
	font-size: 0.9em;
	color: var(--color-text-maxcontrast);
}
.grid-view .filestable tbody tr .thumbnail-wrapper {
	order: 1;
	margin-bottom: 8px;
	position: relative;
	text-align: center;
}

/* Tag search wrapper with mode toggle */
.tag-search-wrapper {
	display: flex;
	align-items: center;
	gap: 0;
	width: 100%;
}
.tag-mode-toggle {
	display: flex;
	align-items: center;
	gap: 1px;
	height: 40px;
	flex-shrink: 0;
	padding: 0 4px;
	cursor: pointer;
	user-select: none;
	border-radius: var(--border-radius);
	transition: background-color 0.2s ease;
}
.tag-mode-toggle .toggle-label {
	font-size: 11px;
	font-weight: 600;
	color: var(--color-text-maxcontrast);
	transition: color 0.3s ease;
	min-width: 32px;
	text-align: center;
}
.tag-mode-toggle:hover .toggle-label {
	color: var(--color-main-text);
}
.tag-mode-toggle .toggle-icon {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 32px;
	height: 32px;
	transition: all 0.3s ease;
}
.tag-mode-toggle .toggle-icon svg {
	width: 32px;
	height: 32px;
	fill: var(--color-primary-element);
	transition: fill 0.3s ease;
}


/* Hide file info columns and table header in grid view */
.grid-view .filestable  td.file-info {
	display: none;
}
.grid-view .filestable thead {
	display: none;
}
.grid-view a.thumbnail-link {
	position: relative;
}

